	module.exports = {"data":{"site":{"siteMetadata":{"title":"Return of the Shadow"}},"markdownRemark":{"id":"/home/kvalencik/git/rots-site/src/pages/news/001659.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>Message 1659 : Mon Mar  6 (Drelidan)   :: 3-07-2017 Hotfix</p>\n</blockquote>\n<p>Balance Tweaks:</p>\n<ul>\n<li>Archery base and scaling damage has been reduced by a modest amount.</li>\n<li>Archery is less dependent on accuracy for hit-chance.  Net hit-\n** chance slightly reduced.</li>\n<li>Raise the strength soft cap, and standardize the strength cap between races.\n** All races now soft cap at 22 strength (other than halflings - they still\nsoft cap at 20 - small buggers).</li>\n</ul>\n<p>Bug Fixes:</p>\n<ul>\n<li>Ride/lead changes:</li>\n<li>\n<ul>\n<li>You cannot ride a mount that is following someone else.</li>\n</ul>\n</li>\n<li>\n<ul>\n<li>You cannot ride a tame-based mount if you are unable to tame that mount.</li>\n</ul>\n</li>\n<li>\n<ul>\n<li>You cannot lead a tame-based mount if you are unable to tame that mount.</li>\n</ul>\n</li>\n<li>Characters up to level 20 can now be deleted without Imm intervention.</li>\n<li>The group leader, and all group members in the room, get kill credit/XP for\n** any character/mob killed by a member of the group.</li>\n<li>Orcs now get XP/fame/exploits for any kills that their followers get even if\n** they are not engaged.</li>\n<li>\n<ul>\n<li>The same applies to rangers/tames and mystics/guardians.</li>\n</ul>\n</li>\n<li>Fix archery crash related to orc followers firing arrows.</li>\n</ul>\n<ul>\n<li>Drelidan</li>\n</ul>","frontmatter":{"title":"News 1659: 3-07-2017 Hotfix","date":"October 09, 2017"}}},"pathContext":{"path":"/news/1659"}}

